// 根据身份筛选菜单和路由
export function filterMenu(menuList,role){
    return menuList.filter(item=>{
        return item.meta.role.indexOf(role) != -1
    }).map(item=>{
        if(item.children){
            item.children = filterMenu(item.children,role)
        }   
        return item
    })
}

// 转换为一级路由
export function superRouter(routes){
    const routerList = []
    const routerSelect = (routes)=>{
        routes.forEach(item=>{
            if(item.children){
                routerSelect(item.children)
            }else{
                routerList.push(item)
            }
        })
    }
    routerSelect(routes)
    return routerList
}
