import { createRouter, createWebHashHistory } from 'vue-router'
import Layout from "@/views/layout/Layout"
import { localGet , pathMap , localRemove} from '@/utils'
import store from "@/store/index"
import {filterMenu , superRouter} from "@/utils/useFilterMenu.js"
import request from '@/utils/axios'

const routes = [
  {
    path: '/',
    name:'layout',
    component: Layout,
    redirect:'/dashboard'
  },
  {
    path: '/login',
    name:"login",
    component: () => import(/* webpackChunkName: "login" */ '../views/login/Login.vue')
  }
  
]

const router = createRouter({
  history: createWebHashHistory(),
  routes
})


// 路由导航守卫
router.beforeEach(async(to, from, next) => {
  store.dispatch('user/setCurrentPatn',to.path)
  document.title = pathMap[to.name] || '系统介绍'
  const token = localGet('token') || ''

  if (to.path == '/login') {
    // 去登录页
    if(token){
      next("/dashboard")
    }else{
      next()
    }
  } else {
    // 去其他页面
    if (!token) {
      // 如果没有，则跳至登录页面
      next({ path: '/login' })
    } else {
      // 判断用户是否刷新或者第一次进入首页
      if(!store.state.user.role){
        try{
          // 获取身份
          const {role} = await request.post('/user/token',{token})
          // 获取路由
          const {menuList} = await request.get('/menu')
          // 筛选路由
          const filterMenus = filterMenu(menuList,role)
          // 转换为一级路由
          const routerMap = superRouter(filterMenus)
          // 添加路由
          routerMap.forEach(item=>{
            let routerItem = {
              path: item.path,
              name:item.name,
              component: ()=>import('@/' + item.component + "/Index.vue")
            }
            router.addRoute('layout',routerItem)
            router.addRoute(
              {
                path:'/404',
                name:"404",
                component: () => import(/* webpackChunkName: "login" */ '../views/404/Index.vue')
              } 
            )
            router.addRoute(
              {
                path:'/:pathMatch(.*)',
                redirect:'/404'
              }
            )
          })

          store.dispatch('user/setRole',role)
          store.dispatch('user/setMenu',filterMenus)
          next({ ...to, replace: true });
        } catch (error) {
          localRemove('token')
        }     
      }else{
        next()
      }   
    }
  }
})

export default router
