
<template>
  <el-menu
    :default-active="defaultActive"
    :collapse="isCollapse"
    :router="true"
  >
    <div class="logoDiv">
      <img src="@/assets/logo.png" class="logo">
      <span class="title">Vue3 myapp</span>
    </div>
      <menu-item v-for="(item,index) in menuList" :menu="item" :key="index"/>
  </el-menu>
</template>


<script>
import {computed,onMounted, onUnmounted} from 'vue'
import { useStore } from 'vuex'
import MenuItem from './MenuItem.vue'
import { debounce } from '@/utils/index'
export default{
    components:{ 
        MenuItem      
    },
    setup () {
        const store = useStore()
        const defaultActive = computed(()=>{
          return store.state.user.currentPath
        }) 
        const isCollapse = computed(()=>{
            return !store.state.app.sidebar.opened
        })
         const menuList = computed(()=>{
          return store.state.user.menu
        })
        const resize = debounce(()=>{
          if(document.body.clientWidth <= 768){
            store.dispatch("app/toggleSidebarIsShow",false)
          }else if(document.body.clientWidth > 768){
            store.dispatch("app/toggleSidebarIsShow",true)
          }
        },100)
        onMounted(()=>{
            window.addEventListener('resize', resize)
        })
        onUnmounted(()=>{
            window.removeEventListener('resize', resize)
        })
        const isShow = computed(()=>{
          return store.state.app.sidebar.isShow
        })
        return {
            isCollapse,
            menuList,
            defaultActive,
            isShow
        }
    }
}
</script>

<style lang="less" scoped>
.logoDiv{
  min-height: 50px;
  display: flex;
  align-items: center;
  overflow: hidden;
  padding-left: 10px;
  .title{
    padding-left: 10px;
    height: 50px;
    font-size: 22px;
    line-height: 50px;
  }
  .logo{
    width: 40px;
    height: 40px;
  }
}

</style>