<template>
  <!-- calss添加了一个样式 -->
  <div  class="tagsbox">
    <div
      @contextmenu.prevent.native ="openMenu(item,$event,index)"
      :class="isActive(item.path)?'active':''"
      class="tagsview"
      v-for="(item, index) in tags"
      :key="index"
      @click="tagsmenu(item)"
    >
      {{ pathMap[item.name] }}
       <!-- 这个地方一定要click加个stop阻止，不然会因为事件冒泡一直触发父元素的点击事件，无法跳转另一个路由 -->
       <!-- 第一个标签不允许删 -->
      <el-icon class="tagsicon" @click.stop="handleClose(item,index)" v-if="!item.undelete"><Close /></el-icon>
    </div>
    <ul v-show="visible" class="contextmenu" :style="{'left':left+'px','top':top+'px'}">
      <li @click.stop="handleClose(selectedTag,selectedIndex)" v-if="selectedTag.name != 'dashboard'">关闭</li>
      <li @click.stop="cleartags($route.path)">关闭所有</li>
    </ul>
  </div>
</template>

<script>
import { useStore } from 'vuex'
import { useRoute , useRouter } from 'vue-router'
import {computed , reactive , toRefs , watch , onMounted} from "vue"
import {pathMap} from "@/utils"
export default {
    setup () {
        const store = useStore()
        const route = useRoute()
        const router = useRouter()
        const rightMenu = reactive({
            //右键菜单隐藏对应布尔值
            visible: false,
            //右键菜单对应位置
            top: 0,
            left: 0,
            selectedTag:{},
            selectedIndex:0
        })
        const tags = computed(()=>{
            return store.state.app.tagViews
        })
        //监听右键菜单的值是否为true，如果是就创建全局监听点击事件，触发closeMenu事件隐藏菜单，如果是false就删除监听
        watch(
            () => rightMenu.visible,
            (value) => {
                if (value) {
                    document.body.addEventListener('click', closeMenu)
                } else {
                    document.body.removeEventListener('click', closeMenu)
                }
            }
        );
        //点击叉号删除的事件
        const handleClose = (item,index)=>{
            //先把长度保存下来后面用来比较做判断条件
            let length = tags.value.length - 1;
            //vuex调方法
            store.commit('app/CLOSE_TAGVIEW',item)
            // 如果关闭的标签不是当前路由的话，就不跳转
            if (item.path !== route.path) {
                closeMenu()
                return;
            }
            // 判断：如果index和length是一样的，那就代表都是一样的长度，就是最后一位，那就往左跳转一个
            if (index === length) {               
              //删除的是最后一位标签，那么就往左边挪一位跳转路由
              router.push({ path: tags.value[index - 1].path }); 
            } else {
              // 如果你点击不是最后一位标签，点的前面的，那就往右边跳转
              router.push({ path: tags.value[index].path });
            }
            closeMenu()
        }
        //点击跳转路由
        const tagsmenu = item=>{
            //判断：当前路由不等于当前选中项的url
            if (route.path !== item.path) {
                //用path的跳转方法把当前项的url当作地址跳转。
                router.push({ path: item.path });
            }
        }
        //通过监听路由添加高亮效果
        const isActive = (path)=>{
            return path === route.path
        }
        //右键事件，显示右键菜单，并固定好位置。
        const openMenu = (tag, e ,index)=> {
          let menuWidth = store.state.app.sidebar.opened?210:54
          rightMenu.selectedTag = tag
          rightMenu.selectedIndex = index
          rightMenu.left = e.clientX - menuWidth + 10  //右键菜单距离左边的距离
          rightMenu.top = e.clientY + 10  //右键菜单距离上面的距离  
          rightMenu.visible = true   
        }
        //隐藏右键菜单
        const closeMenu = ()=> {
          rightMenu.visible = false
        }
        //右键菜单关闭所有选项，触发vuex中的方法，把当前路由当参数传过去用于判断
        const cleartags = (val)=>{
          store.commit('app/CLEAR_TAGVIEW',val)
          closeMenu()
        }

        // 初始化菜单
        store.commit('app/SET_TAGVIEW',{   
            path: route.path,
            name:route.name
        })
        // 监听路由添加菜单
        watch(route,(route)=>{   
          store.commit('app/SET_TAGVIEW',{   
              path: route.path,
              name:route.name
          })
        })
        return {
            tags,
            ...toRefs(rightMenu),
            handleClose,
            tagsmenu,
            isActive,
            openMenu,
            cleartags,
            closeMenu,
            pathMap,
        }
    }
}
</script>

<style lang="less" scoped>
.tagsbox{
    padding: 10px 15px;
    border-bottom: 1px solid  rgba(0, 0, 0, 0.1);
    // box-shadow: 0 1px 12px 0 rgba(0, 0, 0, 0.1);
}
//标签导航样式
.tagsview {
  cursor: pointer;
  margin-right: 10px;
  margin-bottom: 5px;
  height: 26px;
  line-height: 26px;
  padding: 0 8px;
  border: 1px solid #d8dce5;
  border-radius: 5px;
  color: #000;
  font-size: 12px;
  display: inline-block;
}
.tagsicon{
  width: 16px;
  height: 16px;
  vertical-align: -2px;
  margin-left: 4px;
  border-radius: 50%;
}
//叉号鼠标经过样式
.tagsicon:hover{
  background-color: #d8dce5;
}
//标签高亮
.active{
  background-color: #40ba84;
  color: #fff;
}
//右键菜单样式
.contextmenu {
      margin: 0;
      background: #fff;
      z-index: 100;
      position: absolute;
      list-style-type: none;
      padding: 5px 0;
      border-radius: 4px;
      font-size: 12px;
      font-weight: 400;
      color: #333;
      box-shadow: 2px 2px 3px 0 rgba(0, 0, 0, .3);
      li {
        margin: 0;
        padding: 7px 16px;
        cursor: pointer;
      }
      li:hover{
        background: #eee;
      }
}
</style>