const state = {
    role:'',
    menu:[],
    currentPath:'/dashboard'
  }
  
  const mutations = {
    SET_ROLE: (state,payload) => {
      state.role = payload
    },
    SET_MENU:(state,payload)=>{
      state.menu = payload
    },
    SET_CURRENTPATH:(state,payload)=>{
      state.currentPath = payload
    }
  }
  
  const actions = {
      setRole({ commit },payload) {
          commit('SET_ROLE',payload)
      },
      setMenu({ commit },payload) {
          commit('SET_MENU',payload)
      },
      setCurrentPatn({commit},payload){
        commit('SET_CURRENTPATH',payload)
      }
  }
  
  export default {
    namespaced: true,
    state,
    mutations,
    actions
  }