import Mock from "mockjs"
const baseURL = "http://localhost:8080"
const tokens = {
    admin: {
      token: 'admin-token'
    },
    editor: {
      token: 'editor-token'
    }
  }
  
  const users = {
    'admin-token': {
      role: 'admin',
      introduction: 'I am a super administrator',
      name: 'Super Admin'
    },
    'editor-token': {
      role: 'editor',
      introduction: 'I am an editor',
      name: 'Normal Editor'
    }
  }
//用户登录
Mock.mock(baseURL + "/user/login","post",res=>{
    const {username} = JSON.parse(res.body)
    const token = tokens[username]
    if(!token){
        return {
            code: 60204,
            message:'用户名或密码不正确。'
        }
    }
    return {
        code: 200,
        data: {
          ...token,
          role:username
        }
      }
})

// 根据token返回身份
Mock.mock("http://localhost:8080/user/token" , "post" , res=>{
  const {token} = JSON.parse(res.body)
  const info = users[token]
  if(!info){
      return {
          code: 60204,
          message:'token无效。'
      }
  }
  return {
      code: 200,
      data: {
        ...info
      }
    }
})
