import router from "@/router"

const state = {
  sidebar: {
    opened:true,
    isShow: document.body.clientWidth <= 768?false:true
  },
  // tagview数组
  tagViews:[
    {
      path: '/dashboard',
      name:"dashboard",
      undelete:true
    }
  ]
}

const mutations = {
  TOGGLE_SIDEBAR: (state,payload) => {
    if(payload){
      state.sidebar.opened = payload
    }else{
      state.sidebar.opened = !state.sidebar.opened
    }
    
  },
  CLOSE_SIDEBAR:state=>{
    state.sidebar.opened = false
  },
  // 插入单个tagview
  SET_TAGVIEW: (state,payload)=>{
    let result = state.tagViews.findIndex(item=>{
      return item.name === payload.name
    })
    if(result === -1){
      state.tagViews.push(payload)
    }
  },
  // 关闭单个tagview
  CLOSE_TAGVIEW: (state,payload)=>{
    let result = state.tagViews.findIndex(item=>{ return item.name === payload.name})
    state.tagViews.splice(result, 1)
  },
  // 关闭全部tagview
  CLEAR_TAGVIEW: (state,payload)=>{
    state.tagViews = [
      {
        path: '/dashboard',
        name:"dashboard",
        undelete:true
      }
    ]
    if(payload !== '/dashboard'){
      router.push("/dashboard")
    }
  },
  // 菜单是否隐藏切换
  TOGGLE_SIDEBAR_ISSHOW:(state,payload)=>{
    state.sidebar.isShow = payload
  }
}

const actions = {
    toggleSideBar({ commit },payload) {
        commit('TOGGLE_SIDEBAR',payload)
    },
    closeSideBar({ commit }) {
        commit('CLOSE_SIDEBAR')
    },
    toggleSidebarIsShow({commit},payload){
      commit('TOGGLE_SIDEBAR_ISSHOW',payload)
    }
}

export default {
  namespaced: true,
  state,
  mutations,
  actions
}