<template>
    <div :class="classObj" class="app-wapper">
        <!-- 导航菜单 -->
        <sidebar class="sidebar-container"/> 
        <!-- 主要内容 -->
        <div class="main-container">
            <div class="mask" @click="maskClick" v-if="showMask"></div>
            <navbar/>
            <tag-view/>
            <section id="app-main">
            <router-view v-slot="{ Component }">
                <transition name="fade-transform" mode="out-in">
                    <keep-alive :include="cachedViews">
                        <component :is="Component" />
                    </keep-alive>
                </transition>
            </router-view>  
            </section>
        </div>
    </div>
</template>

<script>
import Sidebar from './components/sidebar/SideBar.vue';
import NavBarVue from './components/NavBar.vue';
import { useStore } from 'vuex'
import { computed } from 'vue';
import TagViewVue from './components/tagView/TagView.vue';
import { useRoute } from 'vue-router';
export default {
    components:{
        Sidebar,
        Navbar:NavBarVue,
        TagView:TagViewVue
    },
    setup(){
        const route = useRoute()
        const store = useStore()
        const classObj = computed(()=>{
            if(store.state.app.sidebar.isShow && document.body.clientWidth > 768){
                return !store.state.app.sidebar.opened?'hideSidebar' : 'openSidebar'
            }else if(store.state.app.sidebar.isShow && document.body.clientWidth <= 768){
                return "maskSidebar"
            }else{
                return 'noShowSidebar'
            }
            
        })
        const cachedViews = computed(()=>{
            return store.state.app.tagViews.map(item=>{
                return item.name
            }).value
        })
        const key = computed(()=>{
            return route.path
        })
        const maskClick = ()=>{
            store.dispatch("app/toggleSidebarIsShow",false)
        }
        const showMask = computed(()=>{
            return store.state.app.sidebar.isShow && document.body.clientWidth <= 768
        })
        return{
            classObj,
            cachedViews,
            key,
            maskClick,
            showMask
        }
    }
}
</script>

<style lang="less" scoped>
.sidebar-container{
    position: fixed;
    transition: width 0.28s;
    top: 0;
    bottom: 0;
    left: 0;
    z-index: 1001;
    overflow: hidden;
    height: 100%;
    width: 210px !important;
}
.main-container{
    transition: margin-left 0.28s;
    position: relative;
    min-height: 100%;
    margin-left: 210px;
}
// 菜单关闭
.hideSidebar{
    .sidebar-container{
        width: 54px !important;
    }
    .main-container{
        margin-left: 54px;
    }
}
// 菜单打卡
.openSidebar{
    .sidebar-container{
        width: 210px !important;
    }
    .main-container{
        margin-left: 210px;
    }
}
// 打开菜单和遮罩层
.maskSidebar{
    .sidebar-container{
        width: 210px !important;
    }
    .main-container{
        margin-left: 0px;
    }
}
// 没有菜单
.noShowSidebar{
    .sidebar-container{
        width: 0px !important;
    }
    .main-container{
        margin-left: 0px;
    }
}
// background-color: #EFF1F4;
#app-main {
  /* 50= navbar  50  */
  min-height: calc(100vh - 90px);
  width: 100%;
  position: relative;
  overflow: hidden;
}
.mask{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, .3);
    z-index: 999;
}
</style>