<template>
    <el-sub-menu v-if="menu.children" :index="menu.path">
        <template #title>
            <el-icon>
                <component :is="menu.meta.icon"/>
            </el-icon>
            <span>{{menu.meta.title}}</span>
        </template>
        <MenuItem  v-for="(list,list_index) in menu.children" :menu="list" :key="list_index"/>
    </el-sub-menu>
    <el-menu-item v-else :index="menu.path">
        <el-icon>
            <component :is="menu.meta.icon"/>
        </el-icon>
        <span slot="title">{{menu.meta.title}}</span>
    </el-menu-item>
</template>

<script>
export default {
    name:"MenuItem",
    props:{
        menu:{
            typeof:Object,
            required:true
        }
    },
    setup () {
        return {
            
        }
    }
}
</script>

