import Mock from "mockjs"
const baseURL = "http://localhost:8080"
const  menuList = [
    {
        path: '/dashboard',
        name:"dashboard",
        meta:{role:['admin','editor'],title: "首页",icon: "location"},
        component: 'views/dashboard'
    },
    {
        path: "/system",
        name:"system",
        meta:{role:['admin','editor'],title: "系统管理",icon: "Menu"},
        redirect:"/staff",
        children: [
            {
                path: "/staff",
                name:"staff",
                meta:{role:['admin','editor'],title: "员工管理",icon: "location"},
                component: 'views/system/staff'
            },
            {
                path: "/user",
                name:"user",
                meta:{role:['admin','editor'],title: "角色管理",icon: "location"},
                component: 'views/system/user'
            },
            {
                path: "/log",
                name:"log",
                meta:{role:['admin'],title: "日志管理",icon: "location"},
                redirect:"/loginlog",
                children:[
                    { 
                        path: "/loginlog",
                        name:"loginlog",
                        meta:{role:['admin'],title: "登录日志",icon: "location"},
                        component: 'views/system/log/loginlog'
                    },
                ]
            }
        ]
    },
    {
        path: "/customer",
        name:"customer",
        meta:{role:['admin','editor'],title: "客户管理",icon: "Document"},
        redirect:"/member",
        children: [
            { 
                path: "/member",
                name:"member",
                meta:{role:['admin','editor'],title: "会员管理",icon: "location"},
                component: 'views/customer/member'
            },
            { 
                path: "/order",
                name:"order",
                meta:{role:['admin','editor'],title: "订单管理",icon: "location"},
                component: 'views/customer/order'
            }
        ]
    },
    {
        path:'/func',
        name:"func",
        meta:{role:['admin','editor'],title: "功能",icon: "Document"},
        redirect:"/watermark",
        children: [
            { 
                path: "/watermark",
                name:"watermark",
                meta:{role:['admin','editor'],title: "水印",icon: "location"},
                component: 'views/func/watermark'
            },
            { 
                path: "/table",
                name:"table",
                meta:{role:['admin','editor'],title: "表格",icon: "location"},
                component: 'views/func/table'
            },
            { 
                path: "/baidumap",
                name:"baidumap",
                meta:{role:['admin','editor'],title: "百度地图",icon: "location"},
                component: 'views/func/baidumap'
            }
        ]
    }

]
//导航菜单列表
Mock.mock(baseURL + "/menu","get",req=>{
    return {
        code: 200,
        data: {
            menuList
        }
      }
})