import Mock from "mockjs"
const baseURL = "http://localhost:8080"

//首页表格数据 /dashboard/list
Mock.mock(baseURL + "/dashboard/list","get",()=>{
    return {
        code: 200,
        data: Mock.mock({
            'tableData|20': [{
                    order_no: '@guid()',
                    timestamp: + Mock.Random.date('T'),
                    username: '@name()',
                    price: '@float(1000, 15000, 0, 2)',
                    'status|1': ['success', 'pending']
                }],
            total:20
      })
    }
})

