//  防抖函数
export function debounce(func,time){
    let timer = null;
    return function(){
        if(timer){
            clearTimeout(timer)
        }
        timer = setTimeout(func,time)
    }
}

// 节流函数
export function throttle(func,time){
    let valid = true
    return function(){
        if(!valid){
            return false
        }
        valid = false
        setTimeout(()=>{
            func()
            valid = true
        },time)
    }
}