<template>
    <div class="navbar">
        <hamburger id="hamburger-container" 
        :is-active="isActive" 
        class="hamburger-container" 
        @toggleClick="toggleSideBar" />
        <div class="info">
            <el-space>
                <el-icon @click="screen" class="fullscreen"><FullScreen /></el-icon>
                <el-dropdown @command="handleCommand" trigger="click">
                    <span class="el-dropdown-link">
                        小王
                    <el-icon class="el-icon--right" style="vertical-align: middle;">
                        <arrow-down />
                    </el-icon>
                    </span>
                    <template #dropdown>
                    <el-dropdown-menu>
                        <el-dropdown-item command="dashboard">回到主页</el-dropdown-item>
                        <el-dropdown-item command="loginout">退出登录</el-dropdown-item>
                    </el-dropdown-menu>
                    </template>
                </el-dropdown>
            </el-space>
        </div>
    </div>
</template>

<script>
import Hamburger from "@/components/Hamburger.vue"
import { useStore } from 'vuex'
import { useRouter } from "vue-router" 
import { computed, ref } from "vue"
import { localRemove } from "@/utils"
import screenfull from 'screenfull'
export default {
    components:{
        Hamburger
    },
    setup () {
        const isFullscreen = ref(false)
        const store = useStore()
        const router = useRouter()
        const isActive = computed(()=>{
            return store.state.app.sidebar.opened
        })
        const toggleSideBar = ()=>{
            if(store.state.app.sidebar.isShow){
                store.dispatch('app/toggleSideBar')
            }else{
                store.dispatch('app/toggleSidebarIsShow',true)
            }
            
        }
        const handleCommand = (command)=>{
            if(command === 'dashboard'){
                router.replace("/dashboard")
            }else if(command === 'loginout'){
                localRemove('token')
                router.replace("/login")
            }
        }
        const screen = ()=>{      
            screenfull.toggle();       
        }
        return {
            toggleSideBar,
            isActive,
            handleCommand,
            screen
        }
    }
}
</script>

<style lang="less" scoped>
.navbar{
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 40px;
    border-bottom: 1px solid  rgba(0, 0, 0, 0.1);
}
.el-dropdown-link,.fullscreen{
    cursor: pointer;
}
</style>